<script>
import {
	Hooper,
  Slide,
  Progress as HooperProgress,
  Pagination as HooperPagination,
  Navigation as HooperNavigation
} from 'hooper';

import 'hooper/dist/hooper.css';
import axios from 'axios'
import moment from "moment";
import fs from 'fs';

var page = 21;
var loc = window.location.pathname.split('/').slice(-1)[0];
//let loc = window.location.pathname.split('/').slice(0)[3];

export default {
  name: 'App',
  components: {
			Hooper,
    Slide,
    HooperProgress,
    HooperPagination,
    HooperNavigation
  },
  data() {
    return {
					bsky_pds: "https://bsky.social/xrpc",
					bsky_handle: "yui.syui.ai",
					bsky_cursor: null,
					bsky_cid: "",
					bsky_json: null,
					api_url: null,
					api_json: null,
					comment_open: false,
					comment_first: null,
					loading: true,
					f: null,
					url_check: true,
					url: "/",
					s_a: 0,
					s_b: 2,
					s_c: 41,
					s_d: 51,
					s_e: 60,
      products: [...Array(Number(page)).keys()],
      hooperSettings: {
							itemsToShow: 1,
							centerMode: true,
							initialSlide: loc,
						}
    };
		},
		methods: {
			moment(date) {
				return moment.utc(date).local().format("YYYY.MM.DD");
			},
			page_run(s) {
				this.loc = s;
				this.hooperSettings.initialSlide = s;
			},
			load() {
				this.loading = false;
			},
			axios_check(url) {
				axios.get(url)
					.catch(error => {
						this.url_check = false;
					});
			}
		},
		mounted() {
			if (window.location.host === "localhost:8080") {
				this.api_url = "/api/";
			} else if (window.location.host === "192.168.11.12:8080"){
				this.api_url = "/api/";
			} else {
				if (location.protocol !== "https:") {
					location.replace("https:" + location.href.substring(location.protocol.length));
				}
				this.api_url = "https://api.syui.ai";
			}
			// com.atproto.repo.describeRepo
			// com.atproto.repo.listRecords
			// com.atproto.repo.getRecord
			//let url = this.bsky_pds + "/com.atproto.repo.listRecords?repo=" + this.bsky_handle + "&collection=app.bsky.feed.post";
			//axios
			//	.get(url,{ crossdomain: true })
			//	.then(response => (this.bsky_json = response));
			let url = this.api_url + "/users/2/ma?itemsPerPage=4000";
			axios
				.get(url,{ crossdomain: true })
				.then(
					response => {this.api_json = response
						this.comment_first = this.api_json.data[0]
					}
				);
		}
};
</script>

